/*FIRST SCREEN*/

@media only screen and (max-width: 450px) {
    /* Estilos para teléfonos móviles */
    .content-page{
        width: 100vw;
        text-align: justify;
        padding: 0 8em;
    }
    .content-page__title{
        font-size: 2.5em;
        text-align: center;
    }
    .content-page__text{
        font-size: 1.4em;
    }
    .content-page__span-msg{
        font-size: 1.4em;
    }
    .navbar__logo {
        height: 20px;
        margin-top: 38px;
    }
    .navbar__back-btn{
        left: 15px;
    }
    .action-container__start-btn{
        width:  210px;
        height: 210px;
    }

    .first-screen__intro-container{
        padding: 80px auto;
    }
    .intro-container__title{
        font-size: 2.5em;
    }
    .intro-container__sub-title{
        font-size: 1em;
    }
    .control-panel__intro-btn {
        width: fit-content;
        height: 55px;
    }
    .intro-btn__text{
        margin-top: 15px;
        margin-bottom: 15px;
    }

    .intro-btn__icon{
        width:  25px;
        height: 25px;
    }

    .footer__text{
        text-align: center;
        font-size: 0.9em;
    }
    .footer__action-btn{
        margin: 0 20px;
    }

    .element.active img{
        width: 180px;
    }
    .element.active h2{
        font-size: 1.9em;
    }
    .element.active span{
        font-size: 1.5em;
  }

  .credit__item{
    padding: 15px 40px;
    text-align: center;
  }
  .credit__more-info{
    width: 90%;
  }
}
  